*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --black: #111;
  --grey: #1e1823;
  --orange: #fd9222;
  --dirty-white: #fafafa;
  --white: #fff;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

body {
  height: 100vh;
  font-family: Montserrat;
  position: relative;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.content {
  background-color: var(--dirty-white);
  flex-flow: column;
  display: flex;
}

.list {
  list-style: none;
}

.advantages {
  flex-direction: column;
  flex: 0 0;
  display: flex;
  position: relative;
}

.container-advantages {
  background: var(--grey);
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  padding: 50px 0;
  display: flex;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0, 0, 0, 0);
  white-space: unset;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.advantages-list {
  flex-flow: column;
  flex: 1;
  justify-content: center;
  align-items: flex-start;
  gap: 20px 70px;
  padding: 0 20px;
  display: flex;
}

.svg_circle {
  width: 62px;
  height: 62px;
  border: 1px solid rgba(255, 255, 255, .5);
  border-radius: 365px;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  gap: 7.303px;
  display: flex;
}

.section-two svg {
  fill: currentColor;
}

.item-title {
  color: #fff;
  padding: 22px 0 18px;
  font-size: 18px;
  font-weight: 600;
  line-height: 21.94px;
}

.advantages-description {
  color: rgba(255, 255, 255, .7);
  letter-spacing: -.28px;
  font-size: 14px;
  font-weight: 400;
  line-height: normal;
}

@media screen and (min-width: 768px) {
  .advantages-list {
    flex-flow: wrap;
    justify-content: left;
    padding: 0 50px;
  }

  .advantages {
    flex-basis: calc(50% - 35px);
  }
}

@media screen and (min-width: 1200px) {
  .advantages-list {
    max-width: 1200px;
  }

  .advantages:not(:last-child):after {
    content: "";
    height: 124px;
    width: 1px;
    border: 0 solid rgba(255, 255, 255, .7);
    border-right-width: 1px;
    margin-top: -62px;
    position: absolute;
    top: 50%;
    right: -18px;
  }

  .advantages {
    flex-basis: calc(33.3333% - 46.6667px);
  }
}

