

:root {
    /* page colors */
    --black: #111;
    --grey: #1E1823;
    --orange: #FD9222;
    --dirty-white: #FAFAFA;
    --white: #fff;
    /* page font-style */
    --montserrat: montserrat;
  }
  
  /* Reset styles */
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p {
    margin: 0;
  }
  
  img {
      display: block;
      max-width: 100%;
      height: auto;
    }
  
    body {
      font-family: 'Montserrat', sans-serif;
    }
  
    
    :root {
      font-size: 14px;
      line-height: calc(18 / 14);
      font-weight: 400;
      letter-spacing: -0.02em;
    
      color: var(--primary-color);
      background-color: var(--body-bg-color);
    
      font-synthesis: none;
      text-rendering: optimizeLegibility;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      -webkit-text-size-adjust: 100%;
    }
    
    .visually-hidden {
      position: absolute;
      white-space: nowrap;
      width: 1px;
      height: 1px;
      overflow: hidden;
      border: 0;
      padding: 0;
      clip: rect(0 0 0 0);
      clip-path: inset(50%);
      margin: -1px;
    }
    
    .container {
      width: 100%;
      padding-left: 15px;
      padding-right: 15px;
      margin-left: auto;
      margin-right: auto;
    }
    
    @media screen and (min-width: 375px) {
      .container {
        max-width: 365px;
      }
    }
    
    @media screen and (min-width: 768px) {
      .container {
        max-width: 734px;
      }
    }
    
    @media screen and (min-width: 1200px) {
      .container {
        max-width: 1166px;
      }
    }
    
    .btn {
      padding: 12px 28px;
      border-radius: 100px;
      border: 1px solid var(--white-color);
    
      font-weight: 600;
      line-height: calc(17 / 14);
      color: var(--white-color);
      background-color: transparent;
    
      transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
        border-color 10ms cubic-bezier(0.4, 0, 0.2, 1),
        color 250ms cubic-bezier(0.4, 0, 0.2, 1),
        background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
    
    .btn:hover,
    .btn:focus {
      border-color: transparent;
      color: var(--orange);
      background-color: var(--dirty-white);
      transform: scale(1.1);
    }
    
    .btn:active {
      border-color: transparent;
      color: var(--main-accent-color);
      background-color: var(--dirty-white);
      transform: scale(1.1);
    }
  
    /* Reviews Section */
  .reviews-section {
      padding-bottom: 72px;
      text-align: center;
    }
    
    .reviews-section-tittle {
      margin-bottom: 74px;
    
      font-weight: 600;
      font-size: 20px;
      line-height: 1.2;
      color: var(--black);
      letter-spacing: -0.03em;
    
      text-align: center;
    
      text-transform: uppercase;
    }
    
    .reviews-section-tittle-brown {
      color: var(--orange);
    }
    
    .reviews-list {
      display: flex;
    
      gap: 16px;
    }
    
    .reviews-item-wrapper {
      padding-top: 34px;
    }
    
    .reviews-item {
      position: relative;
    
      text-align: center;
      background-color: var(--white);
      border-radius: 15px;
      border: 1px solid rgba(17, 17, 17, 0.1);
      box-shadow: 0px 9.04932px 26.5717px rgba(215, 228, 249, 0.25);
    }
    
    .reviews-img {
      position: absolute;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
      border-radius: 50%;
    }
    
    .reviews-thumb {
      height: 200px;
    
      padding-top: 54px;
      padding-bottom: 42px;
      padding-left: 18px;
      padding-right: 18px;
    }
    
    .reviews-name {
      margin-bottom: 24px;
    
      font-weight: 600;
      font-size: 18px;
      line-height: 1.33;
      color: var(--primary-color);
    }
    
    .reviews-text {
      font-size: 14px;
      line-height: calc(18 / 14);
      color: rgba(17, 17, 17, 0.7);
    }
    
    .btn.btn-review {
      margin-top: 32px;
      border: 1px solid var(--orange);
      color: var(--primary-color);
      transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
        border-color 30ms cubic-bezier(0.4, 0, 0.2, 1),
        color 250ms cubic-bezier(0.4, 0, 0.2, 1),
        background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
    
    .btn.btn-review:hover,
    .btn.btn-review:focus {
      border-color: transparent;
      color: var(--white-color);
      background-color: var(--orange);
      transform: scale(1.1);
    }
    
    .btn.btn-review:active {
      border-color: transparent;
      color: var(--white-color);
      background-color: var(--orange);
    }
    
    /* ========= Swiper =============*/
    
    @media screen and (min-width: 375px) and (max-width: 767px) {
      .container.slick-content {
        width: 734px;
      }
    }
    
    @media screen and (min-width: 768px) {
      .reviews-section {
        padding-bottom: 100px;
      }
      .container.slick-contant {
        width: 100%;
      }
    
      .reviews-item {
        flex-basis: calc((100% - 16px) / 2);
      }
    
      .reviews-thumb {
        padding-left: 16px;
        padding-right: 16px;
      }
    }
    
    @media screen and (min-width: 1200px) {
      .reviews-section {
        padding-bottom: 128px;
      }
    
      .container.slick-content {
        max-width: 1166px;
      }
    
      .reviews-item {
        flex-basis: calc((100% - 32px) / 3);
      }
    
      .reviews-thumb {
        padding-left: 24px;
        padding-right: 24px;
        padding-bottom: 26px;
        padding-top: 56px;
      }
    
      .slick-track {
        gap: 28px;
      }
    }
    
    /* Leave-Review Modal Window */
  
  .modal-leave-review {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scale(1);
      padding: 32px 24px;
      border-radius: 15px;
    
      min-width: 311px;
      height: 525px;
      max-height: 90vh;
      overflow-y: scroll;
      background-color: var(--dirty-white);
      transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
      display: hidden;
    }
    
    .backdrop.is-hidden .modal-leave-review {
      transform: translate(-50%, -50%) scale(1.1);
    
      transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
    
    .modal-leave-review::-webkit-scrollbar {
      width: 5px;
      background-color: rgba(26, 24, 47, 0.15);
      border-radius: 5px;
      height: 200px;
    }
    
    .modal-leave-review::-webkit-scrollbar-thumb {
      background: transparent;
      border-radius: 5px;
    }
    
    @media screen and (min-height: 707px) {
      .modal-leave-review {
        overflow-y: hidden;
      }
    }
    
    .modal-leave-review-close-btn {
      position: absolute;
      top: 14px;
      right: 14px;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: transparent;
      border: none;
      padding: 6px;
    }
    
    .modal-leave-review-close-btn-icon {
      transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1),
        transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
    
    .modal-leave-review-close-btn-icon:hover,
    .modal-leave-review-close-btn-icon:focus {
      transform: scale(1.1);
      fill: var(--orange);
    }
    
    .modal-leave-review-close-btn-icon:active {
      transform: scale(1.1);
      fill: var(--orange);
    }
    
    .modal-leave-review-text {
      font-weight: 600;
      font-size: 20px;
      line-height: 1.2;
      letter-spacing: -0.03em;
      margin-bottom: 32px;
    }
    
    .modal-leave-review-text-accent {
      color: var(--orange);
    }
    
    .modal-leave-review-form {
      display: flex;
      flex-direction: column;
    }
    
    .modal-leave-review-input {
      padding: 14px 18px;
      width: 100%;
    
      border: 1.5px solid rgba(17, 17, 17, 0.05);
      border-radius: 13px;
      font-size: 14px;
      line-height: 1.21;
      letter-spacing: -0.02em;
    
      box-shadow: 0px 5.44px 5.44px rgba(0, 0, 0, 0.03);
      transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
    
    .modal-leave-review-input:focus,
    .modal-leave-review-input:hover,
    .modal-leave-review-form-message:hover,
    .modal-leave-review-form-message:focus {
      outline: none;
      border-color: var(--orange);
    }
    
    .modal-leave-review-input::placeholder {
      font-size: 12px;
      line-height: 2;
      letter-spacing: 0.04em;
      color: rgba(64, 64, 64, 0.3);
    }
    
    .modal-leave-review-form-filed {
      display: block;
      margin-bottom: 14px;
    }
    
    .modal-leave-review-form-message {
      display: block;
      width: 100%;
      height: 137px;
      padding: 14px 18px;
    
      border: 1.5px solid rgba(17, 17, 17, 0.05);
      border-radius: 13px;
      box-shadow: 0px 5.44px 5.44px rgba(0, 0, 0, 0.03);
      resize: none;
    
      font-size: 18px;
      line-height: 1.22;
      letter-spacing: -0.02em;
      transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
    .modal-leave-review-form-filed-message {
      margin-bottom: 18px;
    }
    
    .modal-leave-review-form-sumbit {
      min-width: 117px;
      padding: 12px 28px;
      background-color: var(--orange);
      border: none;
      align-self: center;
      border-radius: 100px;
    
      font-weight: 600;
      font-size: 14px;
      line-height: 1.21;
      letter-spacing: 0.02em;
      color: var(--white);
      transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
        background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
    
    .modal-leave-review-form-sumbit:hover,
    .modal-leave-review-form-sumbit:focus {
      color: var(--orange);
      background-color: var(--btn-bg-hover);
    }
    
    .modal-leave-review-input::placeholder,
    .modal-leave-review-form-message::placeholder {
      font-size: 14px;
      line-height: 1.21;
      letter-spacing: -0.02em;
      color: rgba(64, 64, 64, 0.3);
    }
    
    .leave-review-input-tel {
      padding-left: 106px;
    }
    
    .modal-leave-review-input-wrapper {
      display: block;
      position: relative;
    }
    
    .leave-review-input-tel-icon {
      position: absolute;
      top: 50%;
      left: 18px;
      transform: translate(0, -50%);
    }
    
    .leave-review-input-tel-text {
      position: absolute;
      top: 50%;
      left: 50px;
      transform: translate(0, -50%);
      font-weight: 500;
      line-height: 1.21;
      letter-spacing: -0.02em;
    }
    
    .leave-review-input-tel-text::after {
      content: '';
      display: inline-block;
      position: absolute;
      top: -5px;
      right: -8px;
      width: 1px;
      height: 32px;
      background-color: rgba(64, 64, 64, 0.3);
    }
    
    /*============ Tablet ============*/
    
    @media screen and (min-width: 768px) {
      .modal-leave-review-close-btn-icon {
        width: 28px;
        height: 28px;
      }
    
      .modal-leave-review {
        padding: 40px 40px;
        border-radius: 15px;
    
        min-width: 517px;
        height: 640px;
        background-color: var(--dirty-white);
      }
    
      .modal-leave-review-text {
        margin-bottom: 40px;
    
        font-size: 24px;
        line-height: 1.17;
      }
    
      .modal-leave-review-form-filed {
        margin-bottom: 19px;
      }
    
      .modal-leave-review-input {
        padding: 22px 24px;
    
        border-radius: 13px;
        font-size: 18px;
        line-height: 1.22;
      }
    
      .leave-review-input-tel {
        padding-left: 177px;
      }
    
      .leave-review-input-tel-text {
        left: 74px;
        font-size: 18px;
        line-height: 1.22;
      }
    
      .leave-review-input-tel-text::after {
        right: -32px;
      }
    
      .leave-review-input-tel-icon {
        width: 32px;
        height: 24px;
        left: 24px;
      }
    
      .modal-leave-review-form-message {
        padding: 22px 24px;
    
        font-size: 18px;
        line-height: 1.22;
      }
    
      .modal-leave-review-input::placeholder,
      .modal-leave-review-form-message::placeholder {
        font-size: 18px;
        line-height: 1.22;
        letter-spacing: -0.02em;
        color: rgba(64, 64, 64, 0.3);
      }
    
      .modal-leave-review-form-sumbit {
        padding: 14px 40px;
      }
    }
    
    .slick {
      margin: 0 -12px;
    }
    
    .slick-track {
      display: flex;
      gap: 16px;
      padding: 6px 0;
    }
    
    .slick .slick-dots {
      display: flex !important;
      align-items: center;
      justify-content: center;
      gap: 16px;
      margin-top: 32px;
    }
    
    .slick .slick-dots li button {
      font-size: 0;
      width: 13px;
      height: 13px;
      border-radius: 50%;
      border: none;
      background-color: rgba(17, 17, 17, 0.1);
      transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
    
    .slick .slick-dots li.slick-active button {
      transform: scale(1.5);
      width: 28px;
      height: 13px;
      border-radius: 30px;
      background-color: var(--orange);
    }
    
    /* slick review */
    
    .slick-chocolate-is-loved {
      margin: 0 -12px;
    }
    
    .slick-chocolate-is-loved .slick-dots {
      display: flex !important;
      align-items: center;
      justify-content: center;
      gap: 16px;
      margin-top: 32px;
    }
    
    .slick-chocolate-is-loved .slick-dots li button {
      font-size: 0;
      width: 13px;
      height: 13px;
      border-radius: 50%;
      border: none;
      background-color: rgba(17, 17, 17, 0.1);
      transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
    
    .slick-chocolate-is-loved .slick-dots li.slick-active button {
      transform: scale(1.5);
      width: 28px;
      height: 13px;
      border-radius: 30px;
      background-color: var(--orange);
    }
    
    .slick-slide img {
      margin: 0 auto;
    }
    